import { Component, OnInit } from '@angular/core';
import {IPost} from './post';
import {PostService} from './posts.service';

// import {PostService} from './posts.service';

@Component({
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.scss']
})
export class PostsComponent implements OnInit {
  errorMessage: string;

  PostFilter: string;
  get postFilter(): string {
    return this.PostFilter;
  }
  set postFilter(value: string) {
    this.PostFilter = value;
    this.filteredPosts = this.postFilter ? this.performFilter(this.postFilter) : this.posts;
  }

  filteredPosts: IPost[];
  posts: IPost[] = [];

  constructor(private postService: PostService) {}

  performFilter(filterBy: string): IPost[] {
    filterBy = filterBy.toLocaleLowerCase();
    return this.posts.filter((post: IPost) => post.title.toLocaleLowerCase().indexOf(filterBy) !== -1);
  }

  ngOnInit(): void {
    this.postService.getPosts().subscribe({
      next: posts => {
        this.posts = posts;
        this.filteredPosts = this.posts;
      },
      error: err => this.errorMessage = err
    });
  }
}
